/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, InputGroup, Button } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Contact", request: "send message" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { language, accountNumber } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /** 
    * Yup schema
    ============================================================================ */
  const schema = yup.object({
    subject: yup.string().required(t("Subject is required")),
  });

  /**
    * Functions
    ============================================================================ */
  const onSubmit = (data) => {
    data.sender = accountNumber;
    data.language = language;
    sendRequest({ req: props.request, data: data });
  };

  /**
    * Form
    ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Card header={false}>
      <Form noValidate onSubmit={handleSubmit}>
        {/* Subject */}
        <Form.Group controlId="validationFormikSubject">
          <Form.Label>{t("Subject")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={t("Enter message subject")}
              aria-describedby="inputGroupPrepend"
              name="subject"
              value={values.subject}
              onChange={handleChange}
              isInvalid={!!errors.subject && touched.subject}
            />
            <Form.Control.Feedback type="invalid">
              {errors.subject}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Message */}
        <Form.Group controlId="validationFormikMessage">
          <Form.Label>{t("Message")}</Form.Label>
          <Form.Control
            placeholder={t("Enter message")}
            name="message"
            value={values.message}
            onChange={handleChange}
            as="textarea"
            rows="10"
          />
        </Form.Group>

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  /**
    * Formik return
    ============================================================================ */
  return (
    <Formik
      component={ComponentForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        subject: "",
        message: "",
      }}
    />
  );
};
